<template>
  <div class="settings">
    <div class="settings__item">
      <div class="settings__header">Profile settings</div>
      <div class="settings__block">
        <div class="settings__title">Series numbers color</div>
        <div class="settings__values">
          <a :class="['settings__color', 'settings__color_red', {'settings__color_active': activeColor === 'red'}]" href="javascript:;" title="Red" @click="setColor('red')"></a>
          <a :class="['settings__color', 'settings__color_violet', {'settings__color_active': activeColor === 'violet'}]" href="javascript:;" title="Violet" @click="setColor('violet')"></a>
          <a :class="['settings__color', 'settings__color_blue', {'settings__color_active': activeColor === 'blue'}]" href="javascript:;" title="Blue" @click="setColor('blue')"></a>
          <a :class="['settings__color', 'settings__color_lightblue', {'settings__color_active': activeColor === 'lightblue'}]" href="javascript:;" title="Light blue" @click="setColor('lightblue')"></a>
          <a :class="['settings__color', 'settings__color_green', {'settings__color_active': activeColor === 'green'}]" href="javascript:;" title="Green" @click="setColor('green')"></a>
          <a :class="['settings__color', 'settings__color_orange', {'settings__color_active': activeColor === 'orange'}]" href="javascript:;" title="Orange" @click="setColor('orange')"></a>
          <a :class="['settings__color', 'settings__color_brown', {'settings__color_active': activeColor === 'brown'}]" href="javascript:;" title="Brown" @click="setColor('brown')"></a>
        </div>
      </div>
    </div>

    <div class="settings__item">
      <div class="settings__header">Categories</div>
      <div class="settings__block">
        <form class="settings__form" @submit.prevent="addCategory">
          <input class="settings__input" type="text" placeholder="New category" v-model="input" />
          <a class="settings__button" href="javascript:;" @click="addCategory">&plus;&nbsp;Add category</a>
        </form>

        <div class="settings__categories">
          <div class="settings__category" v-for="category in categories">
            <span class="settings__label">{{ category }}</span>
            <a class="settings__smallbutton" href="javascript:;" @click="removeCategory(category)">&otimes;</a>
          </div>
        </div>
      </div>
    </div>

    <a class="settings__submit" href="javascript:;" @click="saveSettings">Save changes</a>
    <span :class="['settings__tip', {'settings__tip_visible': tipVisible}]">Saved!</span>
  </div>
</template>

<script>
  export default {
    name: 'SettingsComponent',

    data() {
      return {
        activeColor: this.$store.getters.getUserSettings.activeColor,
        input: '',
        categories: this.$store.getters.getUserSettings.categories,
        tipVisible: false,
      };
    },

    methods: {
      setColor(color) {
        this.activeColor = color;
      },

      addCategory() {
        const categories = [...this.categories];

        if (categories.indexOf(this.input) === -1 && this.input.trim().length > 0) {
          categories.push(this.input);
          this.input = '';
          this.categories = [...categories];
        }
      },

      removeCategory(category) {
        const categories = [...this.categories];
        const index = categories.indexOf(category);
        categories.splice(index, 1);

        this.categories = [...categories];
      },

      saveSettings() {
        const settings = {
          activeColor: this.activeColor,
          categories: this.categories,
        };
        const userID = this.$store.getters.getUser.id_str;

        this.tipVisible = false;

        this.$store.commit('saveUserSettings', settings)

        this.$http.put('https://mentions-api.herokuapp.com/settings', { userID, settings })
          .then((data) => {
            if (data.err) {
              console.log(data.err);
              this.$router.push('/500');
            } else {
              console.log(data);
              this.tipVisible = true;
            }
          },
          (err) => {
            console.log(err);
            this.$router.push('/500');
          });
      },
    },
  };
</script>

<style>
</style>



