<template>
  <div class="timeline">
    <div class="timeline__header">Tweets</div>

    <div class="timeline__item" v-for="tweet in timeline">
      <TweetComponent :data="tweet" />
    </div>
    <!--<button @click="getMoreTweets">Get more</button>-->
  </div>
</template>

<script>
import TweetComponent from '@/components/TweetComponent';

export default {
  name: 'TimelineComponent',

  props: {
    id: {
      type: String,
      default: '',
    },
  },

    components: {
      TweetComponent,
    },

  data() {
    return {
      timeline: () => [],
      count: 100,
      getTimelineUrl: 'https://mentions-api.herokuapp.com/gettimeline',
    };
  },

  computed: {
    user_id() {
      return this.id;
    },
  },

  created() {
    this.getTimeline()
      .then((data) => {
        if (data.err) {
          console.log('error', err);
        } else {
          try {
            const timeline = JSON.parse(data.body.data[1]);

            this.timeline = timeline;
          } catch(e) {
            console.log('e', e);
          }
        }          
      },
      (error) => {
        console.log('error', error);
        this.$router.push('/500');
      });
  },

  methods: {
    getTimeline(id) {
      const auth = this.$store.getters.getAuth;
      let url = `${this.getTimelineUrl}?token=${auth.token}&secret=${auth.secret}&userid=${this.user_id}&count=${this.count}`;

      if (id !== undefined) {
        url += `&maxid=${id}`;
      }

      return this.$http.get(url);
    },

    // getMoreTweets() {
    //   const lastTweet = this.timeline[this.timeline.length - 1];
    //   const id = lastTweet.id;

    //   this.getTimeline(id)
    //     .then((data) => {
    //       if (data.err) {
    //         console.log('error', err);
    //       } else {
    //         try {
    //           let moreTweets = JSON.parse(data.body.data[1]);

    //           if (lastTweet.id === moreTweets[0].id) {
    //             moreTweets = moreTweets.splice(1);
    //           }
              
    //           const addedTimeline = [...this.timeline, ...moreTweets];

    //           this.timeline = addedTimeline;
    //         } catch(e) {
    //           console.log('e', e);
    //         }
    //       }          
    //     },
    //     (error) => {
    //       console.log('error', error);
    //     });
    // },
  },
};
</script>

