<template>
  <div class="page">
    <div class="wrapper wrapper_wide">
      <div class="box box_straight">
        <div class="box__container">
          <HeaderComponent />
          <div class="cover">
            <div class="cover__item">
              <SearchPanelComponent :name="query" />
            </div><!-- /cover__item -->

            <div class="cover__item">
              <ShowcaseComponent :results="searchResults" />
            </div><!-- /cover__item -->
          </div><!-- /cover -->

          <DialogComponent v-show="commenting" :user="commentedUser" />

          <FooterComponent theme="dark" />
        </div><!-- /box-container -->
      </div><!-- /box -->
    </div><!-- /wrapper -->
  </div><!-- /page -->
</template>

<script>
  import HeaderComponent from '@/components/HeaderComponent';
  import SearchPanelComponent from '@/components/SearchPanelComponent';
  import ShowcaseComponent from '@/components/ShowcaseComponent';
  import DialogComponent from '@/components/DialogComponent';
  import FooterComponent from '@/components/FooterComponent';

  export default {
    name: 'SearchPage',

    head: {
      title: 'Search - Tweettoapp',
    },

    components: {
      HeaderComponent,
      SearchPanelComponent,
      ShowcaseComponent,
      DialogComponent,
      FooterComponent,
    },

    data() {
      return {
        searchResults: [],
        commentedUser: {},
        getUsersUrl: 'https://mentions-api.herokuapp.com/searchusers',
        commenting: false,
      };
    },

    computed: {
      query() {
        return this.$route.params.name;
      },
    },

    beforeMount() {
      window.addEventListener('startCommenting', this.startCommenting.bind(this));
      window.addEventListener('cancel', this.abortCommenting.bind(this));
    },

    methods: {
      getResults() {
        const auth = this.$store.getters.getAuth;
        const url = `${this.getUsersUrl}?token=${auth.token}&secret=${auth.secret}&q=${this.query}`;

        this.$http.get(url)
          .then((data) => {
            if (data.err) {
              console.log('error', err);
            } else {
              try {
                const searchResults = JSON.parse(data.body.data[1]);

                this.searchResults = searchResults;

                this.$store.dispatch('saveSearchUsers', searchResults);
              } catch(e) {
                console.log('e', e);
              }
            }          
          },
          (error) => {
            console.log('error', error);
            this.$router.push('/500');
          });
      },

      startCommenting(event) {
        this.commenting = true;

        this.commentedUser = event.detail;
      },

      abortCommenting() {
        this.commenting = false;
      },
    },

    mounted() {
      this.getResults();
    },

    watch: {
      query() {
        this.getResults();
      },
    },
  };
</script>

<style></style>



