<template>
  <div class="page page_alternate">
    <HeaderComponent />
    <ProfileComponent :user="user" :id="id" timelineNeeded />

    <a class="commenter" href="javascript:;" v-show="!commenting" @click="startCommenting">Post comment</a>

    <DialogComponent v-show="commenting" :user="user" :pinned="pinnedTweets" />

    <FooterComponent theme="dark" />
  </div>
</template>

<script>
  import HeaderComponent from '@/components/HeaderComponent';
  import ProfileComponent from '@/components/ProfileComponent';
  import FooterComponent from '@/components/FooterComponent';
  import DialogComponent from '@/components/DialogComponent';

  export default {
    name: 'ProfilePage',

    head: {
      title: 'Profile - Tweettoapp',
    },

    components: {
      HeaderComponent,
      ProfileComponent,
      FooterComponent,
      DialogComponent,
    },

    data() {
      return {
        id: this.$route.params.id,
        user: {},
        getUserUrl: 'https://mentions-api.herokuapp.com/searchuser',
        commenting: false,
        pinned: [],
      };
    },

    computed: {
      pinnedTweets() {
        return this.pinned;
      },
    },

    beforeMount() {
      window.addEventListener('pin', this.pin.bind(this));
      window.addEventListener('unpin', this.unpin.bind(this));
      window.addEventListener('cancel', this.abortCommenting.bind(this));
    },

    created() {
      const savedUsers = this.$store.getSavedUsers;
      if (savedUsers !== undefined && this.id in savedUsers) {
        this.user = savedUsers[this.id];
      } else {
        const auth = this.$store.getters.getAuth;
        const url = `${this.getUserUrl}?token=${auth.token}&secret=${auth.secret}&user_id=${this.id}`;

        this.$http.get(url)
          .then((data) => {
            if (data.err) {
              console.log('error', err);
            } else {
              try {
                const user = JSON.parse(data.body.data[1]);

                this.user = user;

                this.$store.dispatch('saveSearchUsers', [user]);
              } catch(e) {
                console.log('e', e);
              }
            }          
          },
          (error) => {
            console.log('error', error);
            this.$router.push('/500');
          });
      }
    },

    methods: {
      pin(event) {
        this.pinned.push(event.detail);
      },

      unpin(event) {
        const id = event.detail;
        let index = 0;

        for (let i = 0; i < this.pinned.length; i += 1) {
          if (this.pinned[i].id === id) {
            index = i;
            break;
          }
        }

        this.pinned.splice(index, 1);
      },

      startCommenting() {
        this.commenting = true;
      },

      abortCommenting() {
        this.commenting = false;
      },
    },
  };
</script>

<style></style>



