<template>
  <div class="wrapper wrapper_wide">
    <div :class="['wrapper__cover', {'wrapper__cover_lifted': !timelineNeeded}]">
      <img class="wrapper__cover-image" :style="'background-color: #' + user.profile_background_color" :src="background" :alt="user.screen_name" />
    </div>

    <div class="package">
      <div class="package__side">
        <CardComponent form="narrow" :user="user" needLink />
      </div>

      <div class="package__main">
        <SearchPanelComponent :name="user.name" rounded v-if="timelineNeeded" />
        <TimelineComponent :id="id" v-if="timelineNeeded" />
        <SettingsComponent v-if="!timelineNeeded && !tweeting" />
        <TwitterComponent :user="user" v-if="tweeting" />
      </div>
    </div>
  </div>
</template>

<script>
  import SearchPanelComponent from '@/components/SearchPanelComponent';
  import CardComponent from '@/components/CardComponent';
  import TimelineComponent from '@/components/TimelineComponent';
  import SettingsComponent from '@/components/SettingsComponent';
  import TwitterComponent from '@/components/TwitterComponent';

  export default {
    name: 'ProfileComponent',

    components: {
      SearchPanelComponent,
      CardComponent,
      TimelineComponent,
      SettingsComponent,
      TwitterComponent,
    },

    head: {
      title: 'Mentions',
    },

    props: {
      user: {
        type: Object,
        default: () => {},
      },
      id: {
        type: String,
        default: '',
      },
      timelineNeeded: {
        type: Boolean,
        default: false,
      },
      tweeting: {
        type: Boolean,
        default: false,
      },
    },


    computed: {
      background() {
        return this.user.profile_banner_url ||
              this.user.profile_background_image_url_https || '';
      },
    },
  };
</script>

<style></style>



