<template>
  <footer :class="['footer', {'footer_dark':isDark, 'footer_lowered': lowered}]">
    <div class="footer__container">
      <div class="footer__copyright">
        Tweettoapp&nbsp;&copy;&nbsp;2017
      </div>

      <div class="footer__links-container">
        <router-link class="footer__link" to="/terms">Terms and conditions</router-link>
        <router-link class="footer__link" to="/privacy">Privacy</router-link>
        <router-link class="footer__link" to="/contact">Contact us</router-link>
      </div>
    </div>
  </footer>
</template>

<script>
  export default {
    name: 'FooterComponent',

    props: {
      theme: {
        type: String,
        default: 'light',
      },
      lowered: Boolean,
    },

    computed: {
      isDark() {
        return this.theme === 'dark';
      },
    },
  };
</script>

<style>  
</style>



