<template>
  <header class="header">
    <div class="header__container">
      <div class="header__item">
        <router-link class="link" to="/">
          <img class="link__image" src="../assets/images/logo.png" />
        </router-link>
      </div>

      <a class="header__item header__item_mobile" href="javascript:;" @click="toggleMenu">
        <img class="header__hamburger" src="../assets/images/menu.svg" alt="Menu" />
      </a>

      <div :class="['header__item', 'header__item_desktop', { 'header__item_expanded': isMenuExpanded }]">
        <ul :class="['navigation', {'is-logged':isLogged}]">
          <li class="navigation__item">
            <router-link class="navigation__link" to="/landing">About</router-link>
          </li>
          
          <li class="navigation__item navigation__item_logged">
            <router-link class="navigation__link" to="/posted">Posted</router-link>
          </li>
          
          <li class="navigation__item navigation__item_logged">
            <router-link class="navigation__link" to="/notifications">Notifications</router-link>
          </li>
          
          <li class="navigation__item navigation__item_logged">
            <div class="showcase">
              <div class="showcase__item">
                <span class="showcase__title">
                  <span class="showcase__greeting">Hello,</span>
                  <span class="showcase__name">{{ name }}</span>
                </span>
              </div>

              <div class="showcase__item">
                <div :class="['dropdown', { 'is-expanded':isDropdownExpanded }]">
                  <a class="dropdown__toggler" href="javascript:;" @click="expandDropdown">
                    <img class="dropdown__image" :src="user.profile_image_url_https" :alt="name" />
                    <img class="dropdown__icon dropdown__icon_changing" src="../assets/images/show.png" alt="Show" />
                    <img class="dropdown__icon dropdown__icon_changing" src="../assets/images/hide.png" alt="Hide" />
                  </a>

                  <div class="dropdown__container">
                    <span class="dropdown__spacer"></span>
                    <ul class="menu menu_lifted">
                      <li class="menu__item">
                        <router-link class="menu__link" to="/myprofile">
                          <span class="menu__title">My profile</span>
                        </router-link>
                      </li>

                      <li class="menu__item">
                        <router-link class="menu__link" to="/settings">
                          <span class="menu__title">Settings</span>
                        </router-link>
                      </li>
                      
                      <li class="menu__item">
                        <router-link class="menu__link" to="/logout">
                          <span class="menu__title">Logout</span>
                        </router-link>
                      </li>
                    </ul>
                  </div>
                </div>
              </div>
            </div>
          </li>
        </ul>
      </div>
    </div>
  </header>
</template>

<script>
  export default {
    name: 'HeaderComponent',

    data() {
      return {
        user: {},
        name: '',
        isLogged: false,
        isDropdownExpanded: false,
        isMenuExpanded: false,
      };
    },

    created() {
      return this.user = this.$store.getters.getUser;
    },

    methods: {
      expandDropdown() {
        this.isDropdownExpanded = !this.isDropdownExpanded;
      },

      toggleMenu() {
        this.isMenuExpanded = !this.isMenuExpanded;
      },
    },

    watch: {
      user() {
        if (Object.keys(this.user).length > 0 && !this.user.errors) {
          if (this.user.name.length > 0) {
            this.name = this.user.name.split(' ')[0];
          }

          this.isLogged = true;
        }
      },
    },
  };
</script>

<style>
  
</style>



