<template>
  <div class="page page_alternate">
    <HeaderComponent />
    <TitleComponent>Posted</TitleComponent>
    <CommentsComponent :comments="comments" />
    <FooterComponent theme="dark" />
  </div>
</template>

<script>
  import HeaderComponent from '@/components/HeaderComponent';
  import TitleComponent from '@/components/TitleComponent';
  import CommentsComponent from '@/components/CommentsComponent';
  import FooterComponent from '@/components/FooterComponent';

  export default {
    name: 'PostedPage',

    head: {
      title: 'Posted - Tweettoapp',
    },

    data() {
      return {
        comments: [],
        getPostedUrl: 'https://mentions-api.herokuapp.com/',
      };
    },

    components: {
      HeaderComponent,
      TitleComponent,
      CommentsComponent,
      FooterComponent,
    },

    created() {
      const id = this.$store.getters.getUser.id_str;
      const url = `${this.getPostedUrl}user/${id}/mentions`;

      this.$http.get(url)
        .then((data) => {
          if (data.err) {
            console.log('error', err);
          } else {
            console.log(data);
            try {
              const comments = JSON.parse(data.bodyText);

              this.comments = comments;
            } catch(e) {
              console.log('e', e);
            }
          }          
        },
        (error) => {
          console.log('error', error);
        });
    },

    beforeMount() {
      window.addEventListener('deleteComment', this.deleteComment.bind(this));
      window.addEventListener('editComment', this.editComment.bind(this));
    },

    methods: {
      deleteComment(event) {
        const id = event.detail;
        const comments = [...this.comments];
        let index;

        for (let i = 0; i < comments.length; i += 1) {
          if (comments[i]._id === id) {
            index = i;
            break;
          }
        }

        if (index !== undefined) {
          comments.splice(index, 1);
          this.comments = [...comments];

          this.$http.delete(`https://mentions-api.herokuapp.com/mentions/${id}`)
          .then((data) => {
            console.log(data);
          },
          (err) => {
            console.log(err);
            this.$router.push('/500');
          });
        }
      },

      editComment(event) {
        const id = event.detail._id;
        const comments = [...this.comments];
        let index;

        for (let i = 0; i < comments.length; i += 1) {
          if (comments[i]._id === id) {
            index = i;
            break;
          }
        }

        if (index !== undefined) {
          comments.splice(index, 1, event.detail);
          this.comments = [...comments];

          const newComment = {
            text: event.detail.text,
            category: event.detail.category,
            pinned: event.detail.pinned,
          };

          console.log(newComment);

          this.$http.put(`https://mentions-api.herokuapp.com/mentions/${id}`, newComment)
          .then((data) => {
            console.log(data);
          },
          (err) => {
            console.log(err);
            this.$router.push('/500');
          });
        }
      },
    },
  };
</script>

