<template>
  <div class="comment">
    <div class="comment__side">
      <img class="comment__avatar" :src="avatar.huge" :alt="comment.twitterUser.screen_name" />

      <div class="comment__info">
        <div class="comment__title">{{ comment.twitterUser.name }}
          <img class="comment__verified" src="../assets/images/mark.png" alt="verified" v-if="comment.twitterUser.verified" />
        </div>
      </div>

      <router-link class="comment__name" :to="`/profile/${comment.twitterUser.id_str}`">@{{ comment.twitterUser.screen_name }}</router-link>
    </div>

    <div class="comment__main">
      <div class="comment__header">
        <div class="comment__bio">
          <img class="comment__image" :src="comment.user.profile_image_url_https" :alt="comment.user.name" />
          <span class="comment__name comment__name_dark">{{ name }}</span>
          <span class="comment__date">
            <DateComponent :date="comment.created_at" />
          </span>
        </div>

        <div class="comment__category">{{ comment.category }}</div>
      </div>

      <div class="comment__textblock">
        <div class="comment__text">{{ comment.text }}</div>
        <div class="comment__textlinks" v-if="comment.pinned.length > 0">
          <span class="comment__intro">Linked tweets:</span>
          <router-link class="comment__textlink" :to="`https://twitter.com/${comment.screen_name}/status/${link.id}`" v-for="(link, index) in comment.pinned" target="_blank" :key="index">{{ link.text }}</router-link>
        </div>
      </div>

      <div class="comment__controls">
        <div class="comment__socials">
          <router-link class="comment__smallbutton comment__smallbutton_twitter" :to="`https://twitter.com/intent/tweet?text=${linkText}`" target="_blank">
            <img class="comment__trademark" src="../assets/images/twitter.png" alt="Tweet it" />
          </router-link>
          <router-link class="comment__smallbutton comment__smallbutton_facebook" to="https://www.facebook.com/sharer/sharer.php?u=https://abdogasim.github.io">
            <img class="comment__trademark" src="../assets/images/facebook.png" alt="Share on Facebook" />
          </router-link>
        </div>

        <div class="comment__editing">
          <a class="comment__button comment__button_delete" href="javascript:;" @click="confirmDeleteComment">
            <img class="comment__icon" src="../assets/images/cross.png" />
            <span class="comment__label">Delete</span>
          </a>

          <a class="comment__button comment__button_edit" href="javascript:;" @click="confirmEditComment">
            <img class="comment__icon" src="../assets/images/edit.png" />
            <span class="comment__label">Edit comment</span>
          </a>
        </div>
      </div>
    </div>

    <DialogComponent v-show="editing" :user="comment.user" :pinned="comment.pinned" :view="operation" :comment="comment" />
  </div>
</template>

<script>
  import DialogComponent from '@/components/DialogComponent';
  import DateComponent from '@/components/DateComponent';

  export default {
    name: 'CommentComponent',

    components: {
      DialogComponent,
      DateComponent,
    },

    props: {
      comment: {
        type: Object,
        default: () => {},
      },
    },

    data() {
      return {
        editing: false,
        operation: 'deleting',
      };
    },

    beforeMount() {
      window.addEventListener('confirmDeleteComment', this.deleteComment.bind(this));
      window.addEventListener('confirmEditComment', this.editComment.bind(this));
      window.addEventListener('cancel', this.cancelEditingComment.bind(this));
    },

    methods: {
      confirmDeleteComment() {
        this.operation = 'deleting';
        this.editing = true;
      },

      confirmEditComment() {
        this.operation = 'editing';
        this.editing = true;
      },

      cancelEditingComment() {
        this.editing = false;
      },

      deleteComment(event) {
        this.editing = false;

        const deleteEvent = new CustomEvent("deleteComment", {
          detail: event.detail,
        });

        window.dispatchEvent(deleteEvent);
      },

      editComment(event) {
        this.editing = false;

        const editEvent = new CustomEvent("editComment", {
          detail: event.detail,
        });

        window.dispatchEvent(editEvent);
      },
    },

    computed: {
      avatar() {
        const imageUrl = this.comment.twitterUser.profile_image_url_https;
        const imageArr = imageUrl.split('_normal');
        
        const big = imageArr.join('_bigger');
        const huge = imageArr.join('');

        return {
          big,
          huge,
        };
      },

      name() {
        return this.comment.user.name.split(' ')[0];
      },

      linkText() {
        return encodeURI(`See me commenting ${this.comment.twitterUser.name} on Tweettoapp`);
      }
    },
  };
</script>

<style>
</style>



