<template>
  <div :class="['card', {'card_alternate': form === 'narrow'}]" v-if="user.id">
    <div class="card__variant" v-if="form === 'wide'">
      <div class="card__cover" :style="'background-color: #' + user.profile_background_color">
        <span class="card__spacer"></span>
        <div class="card__cover-container" v-if="background">
          <img class="card__cover-image" :src="background" :alt="user.screen_name" />
        </div>
      </div>

      <div class="card__file">
        <img class="card__avatar" :src="avatar.big" :alt="user.screen_name" />

        <div class="card__main">
          <div class="card__title">{{ user.name }}
            <img class="card__verified" src="../assets/images/mark.png" alt="verified" v-if="user.verified" />
          </div>
          <div class="card__since">Joined {{ joined }}</div>
        </div>

        <router-link class="card__name" :to="`/profile/${user.id_str}`">@{{ user.screen_name }}</router-link>

        <div class="card__text">{{ user.description }}</div>

        <div class="card__button-container">
          <a class="card__button" href="javascript:;" @click="startCommenting">
            <img class="card__button-icon" src="../assets/images/comment.png" alt="comment" />
            <span class="card__button-label">Comment</span>
          </a>
        </div>
      </div>
    </div>

    <div class="card__variant" v-else>
      <img class="card__avatar" :src="avatar.huge" :alt="user.screen_name" />
      
      <div class="card__main">
        <div class="card__title">{{ user.name }}
          <img class="card__verified" src="../assets/images/mark.png" alt="verified" v-if="user.verified" />
        </div>
      </div>

      <div class="card__name">@{{ user.screen_name }}</div>

      <div class="card__text">{{ user.description }}</div>

      <div class="card__pair">
        <img class="card__pair-icon" src="../assets/images/place.png" alt="location" />
        <span class="card__pair-label">{{ user.location }}</span>
      </div>

      <div class="card__pair card__pair_link" v-if="user.entities && user.entities.url && user.entities.url.urls && user.entities.url.urls.length > 0 && user.entities.url.urls[0].display_url && user.entities.url.urls[0].url">
        <img class="card__pair-icon" src="../assets/images/link.png" alt="link" />
        <router-link class="card__pair-label" :to="user.entities.url.urls[0].url">{{ user.entities.url.urls[0].display_url }}</router-link>
      </div>

      <div class="card__since">Joined {{ joined }}</div>

      <router-link class="card__visit" :to="`https://twitter.com/${user.screen_name}`" v-if="needLink">
        <img class="card__visit-icon" src="../assets/images/bird.png" alt="" />
        <span class="card__visit-label">Visit my Twitter account</span>
      </router-link>
    </div>
  </div>
</template>

<script>
export default {
  name: 'CardComponent',

  props: {
    user: {
      type: Object,
      default: () => {},
    },
    form: {
      type: String,
      default: 'wide',
    },
    needLink: {
      type: Boolean,
      default: false,
    },
  },

  methods: {
    startCommenting() {
      const commentEvent = new CustomEvent("startCommenting", {
        detail: this.user,
      });

      window.dispatchEvent(commentEvent);
    },
  },

  computed: {
    joined() {
      const MONTHS = [
        'January',
        'February',
        'March',
        'April',
        'May',
        'June',
        'July',
        'August',
        'September',
        'October',
        'November',
        'December',
      ];

      const date = new Date(this.user.created_at);

      const month = MONTHS[date.getMonth()];
      const year = date.getFullYear();

      return `${month} ${year}`;
    },

    background() {
      return this.user.profile_banner_url ||
             this.user.profile_background_image_url_https || '';
    },

    avatar() {
      const imageUrl = this.user.profile_image_url_https;
      const imageArr = imageUrl.split('_normal');
      
      const big = imageArr.join('_bigger');
      const huge = imageArr.join('');

      return {
        big,
        huge,
      };
    },
  },
};
</script>

<style>
</style>



