<template>
  <div class="wrapper">
    <div class="box box_relative">
      <div class="box__container">
        <img class="box__logo" src="../assets/images/logo.png" />

        <div class="welcome">
          <div class="welcome__title">Tweettoapp</div>
          <div class="welcome__label">makes Twitter a better place</div>
          
          <a :class="['welcome__link', {'welcome__link_visible': isLinkVisible}]" :href="loginLink" v-if="!isLogged">
            Get started
          </a>

          <router-link class="welcome__link welcome__link_visible" to="/" v-else>
            Get started
          </router-link>
        </div>          
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    name: 'LoginComponent',

    data: function () {
      return {
        loginQuery: '',
        loginLink: '',
        isLinkVisible: false,
      };
    },
    
    beforeMount() {
      this.$http.get('https://mentions-api.herokuapp.com/auth')
        .then((res) => {
          this.loginQuery = res.body;
          this.isLinkVisible = true;
        },
        (err) => {
          console.log(err);
          this.$router.push('/500');
        });
    },

    watch: {
      loginQuery() {
        this.loginLink = `https://api.twitter.com/oauth/authorize?${this.loginQuery}`;
      },
    },

    computed: {
      isLogged() {
        const user = this.$store.getters.getUser;

        if (Object.keys(user).length > 0 && !user.errors) {
          return true;
        }
        
        return false;
      },
    },
  };
</script>

<style>
</style>



