<template>
  <div class="tweet">
    <div class="tweet__date">
      <DateComponent :date="data.created_at" />
    </div>

    <div class="tweet__text">{{ data.text }}</div>

    <div class="tweet__meta">
      <div class="tweet__counters">
        <div class="tweet__counter">
          <img class="tweet__icon" src="../assets/images/retweet.png" alt="Retweets" />
          <span class="tweet__label">{{ data.retweet_count }}</span>
        </div>

        <div class="tweet__counter">
          <img class="tweet__icon" src="../assets/images/star.png" alt="Stars" />
          <span class="tweet__label">{{ data.favorite_count }}</span>
        </div>
      </div>

      <div class="tweet__control" v-show="!isPinned">
        <div class="tweet__control-container">
          <a class="tweet__button tweet__button_sensitive" href="javascript:;" @click="pin">
            <img class="tweet__button-icon" src="../assets/images/pin.png" alt="Pin" />
          </a>

          <span class="tweet__tip">Link to comment</span>
        </div>
      </div>

      <div class="tweet__control" v-show="isPinned">
        <div class="tweet__control-container">
          <a class="tweet__button" href="javascript:;" @click="unpin">
            <img class="tweet__button-icon" src="../assets/images/pinned.png" alt="Pinned" />
          </a>

          <span class="tweet__tip">Linked to comment</span>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import DateComponent from '@/components/DateComponent';

  export default {
    name: 'TweetComponent',

    components: {
      DateComponent,
    },

    props: {
      data: {
        type: Object,
        default: () => {},
      },
    },

    data() {
      return {
        isPinned: false,
        tweetUrl: `https://twitter.com/${this.data.user.name}/status/`
      };
    },

    created() {
      console.log(this.data);
    },

    methods: {
      pin() {
        const pinEvent = new CustomEvent("pin", {
          detail: {
            id: this.data.id_str,
            name: this.data.user.name,
            screen_name: this.data.user.screen_name,
            text: this.data.text,
          }
        });

        window.dispatchEvent(pinEvent);
        
        this.isPinned = true
      },

      unpin() {
        const unpinEvent = new CustomEvent("unpin", {
          detail: this.data.id_str,
        });

        window.dispatchEvent(unpinEvent);
        
        this.isPinned = false;
      },
    },
  };
</script>

<style>
</style>



