<template>
  <span>{{ formattedDate }}</span>
</template>

<script>
  import moment from 'moment';

  export default {
    name: 'DateComponent',

    props: {
      date: {
        type: String,
      },
    },

    computed: {
      formattedDate() {
        const date = moment(this.date);
        return date.fromNow();
      },
    },
  };
</script>

<style>
</style>



