<template>
  <div class="page page_normal">
    <div class="page page_strict page_bg">
      <LoginComponent />
    </div>
    <AboutComponent />
    <FooterComponent theme="dark" lowered />
  </div>
</template>

<script>
  import AboutComponent from '@/components/AboutComponent';
  import LoginComponent from '@/components/LoginComponent';
  import FooterComponent from '@/components/FooterComponent';

  export default {
    name: 'LandingPage',

    head: {
      title: 'Tweettoapp',
    },

    components: {
      AboutComponent,
      LoginComponent,
      FooterComponent,
    },
  
    data: function () {
      return {
      };
    },

    mounted() {
      const hasQuery = /\?+/g.test(window.location.href);
      
      if (hasQuery) {
        const query = window.location.href.split('?')[1];
        const hasToken = /oauth_token=/.test(query);
        const hasVerifier = /oauth_verifier=/.test(query);
        
        let token;
        let secret;
        let user;

        if (hasToken && hasVerifier) {
          this.$http.get(`https://mentions-api.herokuapp.com/auth/callback?${query}`)
            .then((res) => {
              token = res.body.token;
              secret = res.body.token_secret;
              user = res.body.user;

              return this.$store.dispatch('saveAuth', { token, secret });
            })
            .then(() => {
              return this.$store.dispatch('saveUser', user);
            })
            .then(() => {
              const data = {
                userID: user.id_str,
                name: user.name,
                screen_name: user.screen_name,
                userObj: user,
              };

              return this.$http.put('https://mentions-api.herokuapp.com/users', data);
            })
            .then((data) => {
              if (data.err) {
                console.log(data.err);
                this.$router.push('/500');
              }

              return this.$http.get(`https://mentions-api.herokuapp.com/settings/${user.id_str}`)
            })
            .then((data) => {
              if (data.err) {
                console.log(data.err);
                this.$router.push('/500');
              }
            
              return this.$store.dispatch('saveUserSettings', data.body.settings);
            })
            .then(() => {
              this.$router.push('/');
            })
            .catch((err) => {
              console.log(err);
              this.$router.push('/500');
            });
              
        }
      }
    },
  };
</script>

<style></style>



