<template>
  <div class="commentlist">
    <div class="commentlist__header">
      <div class="commentlist__counter">{{ comments.length }} comments posted</div>

      <div class="commentlist__filterer">
        <div class="commentlist__filterer-label">Filter by category</div>
        
        <div :class="['commentlist__dropdown', {'commentlist__dropdown_visible': isDropdownVisible}]">
          <a class="commentlist__dropdown-link" href="javascript:;" @click="toggleDropdown">
            <span class="commentlist__dropdown-main">All categories</span>
            <img class="commentlist__dropdown-icon commentlist__dropdown-icon_changing" src="../assets/images/show-gray.png" alt="Show" />
            <img class="commentlist__dropdown-icon commentlist__dropdown-icon_changing" src="../assets/images/hide-gray.png" alt="Hide" />
          </a>
          <div class="commentlist__dropdown-container">
            <div class="commentlist__dropdown-list">
              <a class="commentlist__dropdown-item commentlist__dropdown-item_large" href="javascript:;" @click="resetFilter">All categories</a>
              <label class="commentlist__dropdown-item" v-for="category in categories">
                <input class="commentlist__dropdown-checkbox" type="checkbox" :value="category" v-model="filter" />
                <img class="commentlist__dropdown-checked" src="../assets/images/checked.png" alt="Checked" />
                {{ category }}
              </label>
            </div>
          </div>
        </div>
      </div>
    </div>

    <div class="commentlist__item" v-for="comment in filteredList">
      <CommentComponent :comment="comment" />
    </div>

  </div>
</template>

<script>
  import CommentComponent from '@/components/CommentComponent';

  export default {
    name: 'CommentsComponent',

    components: {
      CommentComponent,
    },

    props: {
      comments: {
        type: Array,
        default: () => [],
      },
    },

    data() {
      return {
        filter: [],
        isDropdownVisible: false,
      };
    },

    computed: {
      categories() {
        const array = [];
        this.comments.forEach((item) => {
          if (!array.includes(item.category)) {
            array.push(item.category);
          }
        });

        return array;
      },

      filteredList() {
        if (this.filter.length === 0) {
          return this.comments;
        }

        return this.comments.filter((item) => {
          return this.filter.includes(item.category);
        });
      },
    },

    methods: {
      toggleDropdown() {
        this.isDropdownVisible = !this.isDropdownVisible;
      },

      resetFilter() {
        this.filter = [];
        this.isDropdownVisible = false;
      },
    },
  };
</script>

<style>
</style>



