<template>
  <div class="wrapper wrapper_sticky">

    <!-- Posting -->
    <div class="dialog" v-if="view === 'posting'">
      <a class="dialog__cancel" href="javascript:;" title="Cancel" @click="cancel">
        <img class="dialog__icon" src="../assets/images/cross.png" alt="Close" />
      </a>
      <div class="dialog__header">
        Writing comment
      </div>

      <select class="dialog__select" v-model="selected">
        <option disabled value="">Choose category</option>
        <option v-for="category in categories" :value="category">{{ category }}</option>
      </select>

      <textarea class="dialog__textarea" required v-model="text"></textarea>

      <div class="dialog__controls">
        <a class="dialog__control dialog__control_cancel" href="javascript:;" @click="cancel">Cancel</a>
        <a class="dialog__control dialog__control_approve" href="javascript:;" @click="post" :disabled="isDisabled">Post comment</a>
      </div>

      <div class="dialog__attachments" v-if="pinned.length > 0">
        <div class="dialog__header dialog__header_clear">
          Pinned tweets
        </div>

        <div class="dialog__pinned" v-for="item in pinned">
          <img class="dialog__icon dialog__icon_jailed" src="../assets/images/sheet.png" />
          
          <div class="dialog__tweet">{{ item.text }}</div>

          <a class="dialog__remove" href="javascript:;" @click="unpin(item)">
            <img class="dialog__icon dialog__icon_linked" src="../assets/images/cross-dark.png" />
            <span class="dialog__label">Delete</span>
          </a>
        </div>
      </div>
    </div>

    <!-- Editing -->
    <div class="dialog" v-if="view === 'editing'">
      <a class="dialog__cancel" href="javascript:;" title="Cancel" @click="cancel">
        <img class="dialog__icon" src="../assets/images/cross.png" alt="Close" />
      </a>
      <div class="dialog__header">
        Editing comment
      </div>

      <select class="dialog__select" v-model="selected">
        <option disabled value="">Choose category</option>
        <option v-for="category in categories" :value="category">{{ category }}</option>
      </select>

      <textarea class="dialog__textarea" required v-model="text"></textarea>

      <div class="dialog__controls">
        <a class="dialog__control dialog__control_cancel" href="javascript:;" @click="cancel">Cancel</a>
        <a class="dialog__control dialog__control_approve" href="javascript:;" @click="editComment" :disabled="isDisabled">Edit comment</a>
      </div>

      <div class="dialog__attachments" v-if="pinned.length > 0">
        <div class="dialog__header dialog__header_clear">
          Pinned tweets
        </div>

        <div class="dialog__pinned" v-for="item in workingComment.pinned">
          <img class="dialog__icon dialog__icon_jailed" src="../assets/images/sheet.png" />
          
          <div class="dialog__tweet">{{ item.text }}</div>

          <a class="dialog__remove" href="javascript:;" @click="editingUnpin(item)">
            <img class="dialog__icon dialog__icon_linked" src="../assets/images/cross-dark.png" />
            <span class="dialog__label">Delete</span>
          </a>
        </div>
      </div>
    </div>

    <!-- Deleting -->
    <div class="dialog dialog_alternate" v-if="view === 'deleting'">
      <a class="dialog__cancel" href="javascript:;" title="Cancel" @click="cancel">
        <img class="dialog__icon" src="../assets/images/cross.png" alt="Close" />
      </a>
      <div class="dialog__header">
        Deleting comment
      </div>

      <div class="dialog__title">Are you sure you want to delete<br />this comment?</div>

      <div class="dialog__box">
        <div class="comment comment_alternate">
          <div class="comment__header">
            <div class="comment__bio">
              <img class="comment__image" :src="comment.user.profile_image_url_https" :alt="comment.user.name" />
              <span class="comment__name comment__name_dark">{{ name }}</span>
              <span class="comment__date">
                <DateComponent :date="comment.created_at" />
              </span>
            </div>

            <div class="comment__category">{{ comment.category }}</div>
          </div>

          <div class="comment__textblock">
            <div class="comment__text">{{ comment.text }}</div>
            <div class="comment__textlinks" v-if="pinned.length > 0">
              <span class="comment__intro">Linked tweets:</span>
              <router-link class="comment__textlink" :to="`https://twitter.com/${link.screen_name}/status/${link.id}`" v-for="(link, index) in pinned" target="_blank" :key="index">{{ link.text }}</router-link>
            </div>
          </div>
        </div>
      </div>

      <div class="dialog__warn">Warning: once deleted a comment cannot be restored! Make sure you want to delete the comment.</div>

      <div class="dialog__controls">
        <a class="dialog__control dialog__control_delete" href="javascript:;" @click="deleteComment()">Delete the comment</a>
      </div>
    </div>
  </div>
</template>

<script>
  import DateComponent from '@/components/DateComponent';

  export default {
    name: 'DialogComponent',

    components: {
      DateComponent,
    },

    props: {
      pinned: {
        type: Array,
        default: () => [],
      },
      user: {
        type: Object,
        default: () => {},
      },
      view: {
        type: String,
        default: 'posting',
      },
      comment: {
        type: Object,
      },
    },

    data() {
      let defaultText;
      let defaultSelected;

      if (this.comment) {
        defaultText = this.comment.text ? this.comment.text : '';
        defaultSelected = this.comment.category ? this.comment.category : '';
      }      

      return {        
        selected: defaultSelected,
        text: defaultText,
        postLink: 'https://mentions-api.herokuapp.com/twitter',
        isDisabled: false,
        workingComment: this.comment,
        workingPinned: this.pinned,
      };
    },

    computed: {
      categories() {
        return this.$store.getters.getCategories;
      },

      name() {
        return this.comment.user.name.split(' ')[0];
      },

      workingComment() {
        return this.comment;
      },
    },

    methods: {
      clearFields() {
        this.selected = '';
        this.text = '';
        this.workingPinned = [];
      },

      cancel() {
        const cancelEvent = new CustomEvent("cancel");

        window.dispatchEvent(cancelEvent);
      },

      unpin(item) {
        const unpinEvent = new CustomEvent("unpin", {
          detail: item,
        });

        window.dispatchEvent(unpinEvent);
      },

      post() {
        if (this.isDisabled) {
          return;
        }

        this.isDisabled = true;

        const user = this.$store.getters.getUser;

        const data = {
          text: this.text,
          category: this.selected,
          userName: user.screen_name,
          userID: user.id_str,
          twitterName: this.user.screen_name,
          pinned: this.pinned,
          twitterUser: this.user,
          user,
          createdAt: new Date(),
        };

        console.log(data);

        const url = `${this.postLink}/${this.user.id_str}/mentions`;

        this.$http.post(url, data)
          .then((res) => {
            console.log(res);
            this.isDisabled = false;
            this.clearFields();
            this.cancel();
          },
          (err) => {
            console.log(err);
            this.isDisabled = false;
            this.$router.push('/500');
          });
      },

      deleteComment() {
        const confirmDeleteEvent = new CustomEvent("confirmDeleteComment", {
          detail: this.comment._id,
        });

        window.dispatchEvent(confirmDeleteEvent);
      },

      editComment() {
        const comment = Object.assign({}, this.workingComment);

        comment.text = this.text;
        comment.category = this.selected;
        comment.pinned = [...this.workingPinned];

        const confirmEditEvent = new CustomEvent("confirmEditComment", {
          detail: comment,
        });

        window.dispatchEvent(confirmEditEvent);
      },

      editingUnpin(item) {
        const id = item.id;
        const pinned = [...this.comment.pinned];
        let index;

        for (let i = 0; i < pinned.length; i += 1) {
          if (pinned[i].id === id) {
            index = i;
            break;
          }
        }

        if (index !== undefined) {
          pinned.splice(index, 1);
          this.workingPinned = [...pinned];
          this.workingComment.pinned = [...pinned];
        }
      },
    },
  };
</script>

<style>
</style>



