<template>
  <div class="page page_padded">
    <HeaderComponent />
    <TitleComponent>Settings</TitleComponent>
    <ProfileComponent :user="user" :id="id" />
    <FooterComponent theme="dark" />
  </div>
</template>

<script>
  import HeaderComponent from '@/components/HeaderComponent';
  import TitleComponent from '@/components/TitleComponent';
  import ProfileComponent from '@/components/ProfileComponent';
  import FooterComponent from '@/components/FooterComponent';

  export default {
    name: 'SettingsPage',

    head: {
      title: 'Settings - Tweettoapp',
    },    

    components: {
      HeaderComponent,
      TitleComponent,
      ProfileComponent,
      FooterComponent,
    },

    data() {
      const user = this.$store.getters.getUser;

      return {
        id: user.id_str,
        user,
      };
    },
  };
</script>

<style></style>



