<template>
  <div :class="['wrapper', 'wrapper_wide', 'wrapper_bg', {wrapper_rounded: rounded}]">
    <form class="form form_alternate" @submit.prevent="goToSearch">
      <img class="form__sign" src="../assets/images/at.png" alt="at" />
      <input class="form__input" type="text" placeholder="Search for account to add comment" v-model="query" :value="name" />
      <a class="form__submit" href="javascript:;" @click.prevent="goToSearch">
        <img class="form__icon form__icon_regular" src="../assets/images/lens-white.png" alt="lens" />
        <span class="form__label">Search</span>
      </a>
    </form>
  </div>
</template>

<script>
  export default {
    name: 'SearchPanelComponent',

    head: {
      title: 'Mentions',
    },

    props: {
      name: {
        type: String,
        default: '',
      },
      rounded: {
        type: Boolean,
        default: false,
      },
    },

    data() {
      return {
        query: this.name || '',
      };
    },

    methods: {
      goToSearch() {
        this.$router.push(`/search/${this.query}`);
      },
    },
  };
</script>

<style></style>



