<template>
  <div class="twitter">
    <div class="twitter__header">Divide this tweet</div>
    
    <div class="twitter__container">
      <img class="twitter__avatar" :src="user.profile_image_url_https" :alt="user.name" />

      <div class="twitter__field">
        <textarea class="twitter__input" v-model="tweet" :disabled="process === 'success'" @focus="resetProcess"></textarea>
        <span :class="`twitter__count twitter__count_${activeColor}`">{{ count }}</span>
      </div>
    </div>

    <div class="twitter__controls">
      <span class="twitter__counter">{{ counter }}</span>

      <a :class="['twitter__button', 'twitter__button_divide', {'twitter__button_disabled': !singleTweetDisabled}]" href="javascript:;" @click="divideAndTweet">
        <img class="twitter__icon" src="../assets/images/divide-icon.png" alt="Divide tweet" />
        <span class="twitter__label">Divide and tweet</span>
      </a>

      <a :class="['twitter__button', 'twitter__button_tweet', {'twitter__button_disabled': singleTweetDisabled}]" href="javascript:;" @click="sendSingleTweet">
        <img class="twitter__icon" src="../assets/images/tweet.png" alt="Tweet" />
        <span class="twitter__label">Tweet it</span>
      </a>

      <span class="twitter__message twitter__message_sending" v-show="process === 'sending'">Sending</span>
      <span class="twitter__message twitter__message_success" v-show="process === 'success'">Sent</span>
      <span class="twitter__message twitter__message_fail" v-show="process === 'fail'">Failed</span>
    </div>
  </div>
</template>

<script>
  export default {
    name: 'TwitterComponent',

    props: {
      user: {
        type: Object,
        default: () => {},
      },
    },

    data() {
      return {
        activeColor: this.$store.getters.getUserSettings.activeColor,
        tweet: '',
        tweetUrl: 'https://mentions-api.herokuapp.com/tweet',
        process: 'writing',
      };
    },

    computed: {
      counter() {
        return this.tweet.length;
      },

      singleTweetDisabled() {
        return this.tweet.length > 140;
      },

      count() {
        if (this.tweet.length === 0) {
          return 1;
        } else if (this.tweet.length % 140 === 0) {
          return this.tweet.length / 140;
        }

        return Math.floor(this.tweet.length / 140) + 1;
      },
    },

    methods: {
      resetProcess() {
        this.process = 'writing';
      },

      resetForm() {
        this.resetProcess();
        this.tweet = '';
      },

      sendSingleTweet() {
        if (this.singleTweetDisabled) {
          return;
        }

        this.process = 'sending';

        const auth = this.$store.getters.getAuth;
        const status = encodeURI(this.tweet);
        const url = `${this.tweetUrl}?token=${auth.token}&secret=${auth.secret}&status=${status}`;

        this.$http.post(url)
          .then((data) => {
            if (data.err) {
              console.log('error', err);
              this.process = 'fail';
            } else {
              this.process = 'success';
              setTimeout(this.resetForm.bind(this), 5000);
            }          
          },
          (error) => {
            console.log('error', error);
            this.process = 'fail';
          });
      },

      divideAndTweet() {
        if (!this.singleTweetDisabled) {
          return;
        }

        const stringArray = [];

        for (let i = 0; i < this.count; i += 1) {
          const str = this.tweet.substr(i * 140, 140);
          stringArray.push(str);
        }

        this.tweetDivided(stringArray.reverse());
      },

      tweetDivided(array) {
        for (let i = 0; i < array.length; i += 1) {
          this.sendDividedTweet(array[i]);
        }
      },

      sendDividedTweet(str) {
        console.log(str);
        this.process = 'sending';

        const auth = this.$store.getters.getAuth;
        const status = encodeURI(str);
        const url = `${this.tweetUrl}?token=${auth.token}&secret=${auth.secret}&status=${status}`;

        this.$http.post(url)
          .then((data) => {
            if (data.err) {
              console.log('error', err);
              this.process = 'fail';
            } else {
              console.log(data);
              this.process = 'success';
              setTimeout(this.resetForm.bind(this), 5000);
            }          
          },
          (error) => {
            console.log('error', error);
            this.process = 'fail';
          });
      }
    },
  };
</script>

<style>
</style>



