<template>
  <div class="wrapper">
    <div class="widget">
      <div class="widget__header">{{ headerText }}</div>
        <div class="widget__container">
          <div class="feed">
            <div class="feed__item" v-for="user in results">
              <CardComponent :user="user" />
            </div><!-- /feed__item -->
          </div><!-- /feed -->
        </div><!-- /widget__container -->
      </div><!-- /widget__header -->
    </div><!-- /widget -->
  </div><!-- /wrapper -->
</template>

<script>
  import CardComponent from '@/components/CardComponent';
  
  export default {
    name: 'ShowcaseComponent',

    head: {
      title: 'Mentions',
    },

    components: {
      CardComponent,
    },

    props: {
      results: {
        type: Array,
        default: () => [],
      },
    },

    data() {
      return {
        query: this.$route.params.name,
      };
    },

    computed: {
      headerText() {
        const resultsLength = this.results.length;
        const s = resultsLength === 1 ? '' : 's';

        return `${resultsLength} result${s} found`;
      },
    },
  };
</script>

<style></style>



