<template>
  <div class="wrapper">
      <div class="box">
        <div class="box__container">
          <form class="form form_search" @submit.prevent="goToSearch">
            <div class="form__field">
              <div class="dropdown dropdown_high is-expanded">
                <input class="form__input" type="text" placeholder="Search for twitter account" v-model="query" />
                <div class="dropdown__container">
                  <span class="dropdown__spacer"></span>
                  <ul class="list" v-show="searchResults.length > 0">
                    <li class="list__item" v-for="result in searchResults">
                      <router-link class="list__link" :to="`/profile/${result.id_str}`">
                        <img class="list__avatar" :src="result.profile_image_url_https" :alt="result.screen_name" />
                        <span class="list__info">
                          <span class="list__line">
                            <span class="list__title">{{ result.name }}</span>
                            <img class="list__mark" src="../assets/images/mark.png" alt="verified" v-if="result.verified" />
                          </span>

                          <span class="list__meta">@{{ result.screen_name }}</span>
                        </span>
                      </router-link>
                    </li>
                  </ul>
                </div>
              </div>
              <img class="form__icon" src="../assets/images/lens.png" alt="lens" />
            </div>               
          </form>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'SearchComponent',

  data: function () {
    return {
      query: '',
      isDebounced: false,
      request: null,
      getUsersUrl: 'https://mentions-api.herokuapp.com/searchusers',
      searchResults: [],
    };
  },

  methods: {
    goToSearch() {
      this.$router.push(`/search/${this.query}`);
    },

    getTwitters() {
      this.isDebounced = false;

      const auth = this.$store.getters.getAuth;
      const url = `${this.getUsersUrl}?token=${auth.token}&secret=${auth.secret}&q=${this.query}`;

      this.$http.get(url, {
        before: (xhr) => {
          if (this.request) {
            this.request.abort();
          }

          this.request = xhr;
        },
      })
        .then((data) => {
          this.request = null;
          if (data.err) {
            console.log('error', err);
          } else {
            try {
              const searchResults = JSON.parse(data.body.data[1]);
              
              if(searchResults.length > 3) {
                searchResults.length = 3;
              }

              this.searchResults = searchResults;

              this.$store.dispatch('saveSearchUsers', searchResults);
            } catch(e) {
              console.log('e', e);
            }
          }          
        },
        (error) => {
          this.isDebounced = false;
          this.request = null;
          console.log('error', error);
        });
    },

    debounce() {
      if (!this.isDebounced) {
        this.isDebounced = true;

        setTimeout(this.getTwitters.bind(this), 100);
      }
    }
  },

  watch: {
    query() {
      this.debounce();
    }
  }
};
</script>

<style>
</style>



